.AboutDiv {
     background-color: #fafafa;
}

.childDiv {
     font-family: 'Montserrat', sans-serif;
}

.Hr {
     border: .15rem solid black;
     width: 3rem;
     border-radius: .3rem;
}

.underLine {
     border: .2rem solid black;
     width: 3rem;
     border-radius: .3rem;
}

.aboutHead {
     font-weight: 600;
     margin-bottom: 2rem;
}

.paraTag {
     padding: 0rem 15rem;
     text-align: center;
     font-size: 1.1rem;
     font-weight: 400;
}

.skillP {
     background-color: rgba(153, 153, 153, .2);
     font-weight: 500;
     color: #666;
     border-radius: .2rem;
     font-size: .95rem;
}

.contactBtn {
     padding: .9rem 4rem;
     background-color: rgba(144, 144, 144, .3);
     font-weight: 600;
     color: #666;
     border: none;
     border-radius: .3rem;
}