.contactimg
{    background: url('../../../public/BackForHome.jpeg');
    opacity: 0.2;
    width: 100%;
    height: 155vh;
    z-index: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: absolute;
}
.maincontact
{
    font-family: 'Montserrat', sans-serif;
    z-index: 0;
    height: 155vh;
}
.contacthead
{
    font-weight: 600;
    margin-top: 8rem;
}
.contactpara
{
    padding: 0rem 25rem !important;
}
.contactbox
{
    background-color: white;
    width: 55vw;
    z-index: 1;
    border-radius: .2rem;
}
.contactmebutt
{
    background-color: #000000;
    opacity: .8;
    color: #FFFFFF;
    border-radius: .3rem;
    padding: .8rem 3rem;
    font-size: 1.1rem;
    font-weight: 500;

}
.contactinput
{
    color: #333;
    background-color:#F0F0F0 ;
    border: none;
    border-radius: .2rem;
    padding: 1rem;
    font-size: .9rem;
    font-weight: 600;
}
.contactlabel
{
    font-size: .85rem;
    font-weight: 600;
    color: #666;
}